.App {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  justify-content: center;
  align-items: center;
  background-color: #f2ee9d;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App > h2 {
  margin: 0px;
  color: #379b46;
}

.App > p {
  font-size: medium;
  color: #379b46;
}

.App > p > a {
  text-decoration: none;
}
